:root {
  --padding: 1.5rem 5vw;
  --width: 85%;
  --small-screen-width: 95%;
}

.search-posts {
  grid-column: -3 / -1;
  align-self: end;
  padding: 0 5vw;
}

.search-posts input {
  width: 100%;
  height: 3rem;

  color: var(--color-quote);
  font-size: 1em;
  border: none;
  border-bottom: 1px dotted var(--color-quote);
}


/*------ actual post content -------*/
.post {
  width: var(--width);
}

.post>* {
  padding: var(--padding);
  box-shadow: .25rem .25rem 15px var(--color-secondary);
  font-family: 'Times New Roman', Times, serif  !important;
}

.post>.title {
  font-size: 1.5rem;
  font-weight: 600;
}

.post>.content * {
/* if I don't re declare font-family here, 
  safari doesn't take the correct font size on post code area, 
 chrome works without  it. taking from parent container*/
  font-family: 'Times New Roman', Times, serif  !important; 

  font-size: .9rem !important;
  max-width: 100%;
}

.post>.content  h1, h1>span {
  font-size: 1.4rem !important;
}
.post>.content  h2, h2>span {
  font-size: 1.1rem !important;
}


element.style {
  background-color: #f2f2f2;
  box-sizing: inherit;
  margin-bottom: 0px;
  margin-top: 56px;
  overflow-x: auto;
  padding: 20px;
}
.post>.content * {
  font-family: 'Times New Roman', Times, serif !important;
  font-size: .9rem !important;
  max-width: 100%;
}
*, *::before, *::after {
  box-sizing: border-box;
}

.post>.content pre {
  margin: 2rem 30%  1rem 5% !important;
  background-color: #eeebeb !important;
}
/* .post>.content pre *{
   font-family: var(--font-family) !important;
} */
/* Medium devices (landscape tablets, 768px and up) */
@media (max-width: 599px) {
  .search-posts {
    grid-column: 1 / -1;
  }
  .post {
    width: var(--small-screen-width);
  }
  .blog-post>.inner-container>.cards-container {
    padding:  1rem !important;
  }
  .post>.content pre {
    margin: 2rem 1rem !important;
  }

}

@media (min-width: 600px) and (max-width: 768px) {
  .post {
    width: var(--small-screen-width);
  }
  .post>.content pre {
    margin: 1rem !important;
  }
}