.word-cloud {
    background: #f9f8f8;
    border: solid .5px #ddd;
    margin: 2rem;
    border-radius: 2rem;
}

.word-cloud>header {
    font: small-caps 500 1.6rem var(--font-family);
    text-shadow: .2rem .3rem .5rem var(--color-shadow);
    color: var(--color-shadow);
    border-radius: 1rem;
    margin: 0;
    margin-bottom: .5rem;
}

@media (max-width: 599px) {
    .word-cloud {
        margin: 0rem .5rem 1rem .5rem;
        border-radius: 1rem;
    }

    .word-cloud>header {
        font: small-caps 500 1.5rem var(--font-family);
        border-radius: 1rem;
        margin: 0;
    }
}